<template>
  <div>
    <h3 class="main-title">
      
      <div class="pull-right">
        <button class="btn btn-success" @click="sub()"></button>
        <button class="btn btn-danger" @click="del()"></button>
      </div>
    </h3>
    <form class="form-horizontal normal-wrap">
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{business.number}}</p>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{business.name}}</p>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{business.contractAmount===''?'':`${business.contractAmount}`}}</p>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{bill.proposer.name}}</p>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{bill.proposer.tele}}</p>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{business.institution.name}}</p>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{business.institution.telephone}}</p>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <div class="row">
            <div class="col-sm-6">
              <p class="form-control-static">{{business.time.start}}</p>
            </div>
            <div class="col-sm-6">
              <p class="form-control-static">{{business.time.end}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{business.contractType.name}}</p>
        </div>
      </div>
      <div class="form-group" v-if="contractTypeChan">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <div class="row form-group">
            <div class="col-sm-6">
              <p class="form-control-static">
                {{business.contractType.basicFee.main.name}}
              </p>
            </div>
            <div class="col-sm-5">
              <p class="form-control-static">
                {{business.contractType.basicFee.main.percentage}}%
              </p>
            </div>
          </div>
          <div class="row form-group" v-for="(DEPEND, index) in business.contractType.basicFee.depend" :key="index">
            <div class="col-sm-6">
              <p class="form-control-static">
                {{DEPEND.name}}
              </p>
            </div>
            <div class="col-sm-5">
              <p class="form-control-static">
                {{DEPEND.percentage}}%
              </p>
            </div>
          </div>
        </div>
      </div>
      <div class="form-group" v-if="contractTypeChan">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <div class="row form-group">
            <div class="col-sm-6">
              <p class="form-control-static">
                {{business.contractType.benefitFee.main.name}}
              </p>
            </div>
            <div class="col-sm-5">
              <p class="form-control-static">
                {{business.contractType.benefitFee.main.percentage}}%
              </p>
            </div>
          </div>
          <div class="row form-group" v-for="(DEPEND, index) in business.contractType.benefitFee.depend" :key="index">
            <div class="col-sm-6">
              <p class="form-control-static">
                {{DEPEND.name}}
              </p>
            </div>
            <div class="col-sm-5">
              <p class="form-control-static">
                {{DEPEND.percentage}}%
              </p>
            </div>
          </div>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <p class="form-control-static">{{bill.addUpAmount+''}}</p>
        </div>
      </div>
      <hr>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <div class="input-group">
            <input type="number" class="form-control" placeholder="" v-model.number="bill.amount">
            <div class="input-group-addon"></div>
          </div>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.billingUnit">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <label class="radio-inline">
            <input type="radio" name="billType" value="" v-model="bill.type"> 
          </label>
          <label class="radio-inline">
            <input type="radio" name="billType" value="" v-model="bill.type"> 
          </label>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.unit.name">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.taxpayerID">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"><span class="text-danger" v-show="bill.type===''">*</span></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.unit.address">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"><span class="text-danger" v-show="bill.type===''">*</span></label>
        <div class="col-sm-9">
          <input type="tel" class="form-control" placeholder="" v-model="bill.unit.tele">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"><span class="text-danger" v-show="bill.type===''">*</span></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.unit.depositBank">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"><span class="text-danger" v-show="bill.type===''">*</span></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.unit.account">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="date" class="form-control" placeholder="" v-model="bill.filingDate">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="date" class="form-control" placeholder="" v-model="bill.billingDate">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <label class="radio-inline">
            <input type="radio" name="sendWay" value="" v-model="bill.way"> 
          </label>
          <label class="radio-inline">
            <input type="radio" name="sendWay" value="" v-model="bill.way"> 
          </label>
        </div>
      </div>
      <div class="form-group" v-show="bill.way===''">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.receiver">
        </div>
      </div>
      <div class="form-group" v-show="bill.way===''">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.receiveAdd">
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-9">
          <input type="text" class="form-control" placeholder="" v-model="bill.content">
        </div>
      </div>
    </form>
    <bill-sub-modal v-if="showSubModal" :initBill="bill" :initBusiness="business" @submited="submited" @canceled="subCanceled"></bill-sub-modal>
    <bill-del-modal v-if="showDelModal" :initBill="bill" @deleted="deleted" @canceled="delCanceled"></bill-del-modal>
  </div>
</template>

<script>
import Vue from 'vue';
import { Message } from 'element-ui';

import billSubModal from './billSubModal.vue';
import billDelModal from './billDelModal.vue';

Vue.prototype.$message = Message;

export default {
  name: 'billingInforEditor',
  data() {
    return {
      paths: [
        { name: '', url: '/business-handle-list-sales', present: false },
        { name: '', url: `/business-handle-detail-sales-${this.$route.params.id}/billing-infor`, present: false },
        { name: '', url: `/business-handle-detail-sales-${this.$route.params.id}/billing-infor`, present: false },
        { name: '', url: `/business-handle-detail-sales-${this.$route.params.id}/billing-infor/billing-infor-editor`, present: true }
      ],
      business: this.initBusiness,
      bill: {
        id: '',
        proposer: {
          id: this.user.id,
          name: this.user.name,
          tele: this.user.telephone
        },
        addUpAmount: 0,
        amount: 0,
        billingUnit: '',
        type: '',
        unit: {
          name: '',
          address: '',
          tele: '',
          depositBank: '',
          account: ''
        },
        taxpayerID: '',
        filingDate: (() => {
          let t = new Date();
          let Y = t.getFullYear();
          let M = (t.getMonth() + 1 < 10) ? `0${t.getMonth() + 1}` : `${t.getMonth() + 1}`;
          let D = (t.getDate() < 10) ? `0${t.getDate()}` : `${t.getDate()}`;
          return `${Y}-${M}-${D}`;
        })(),
        billingDate: (() => {
          let t = new Date();
          let Y = t.getFullYear();
          let M = (t.getMonth() + 1 < 10) ? `0${t.getMonth() + 1}` : `${t.getMonth() + 1}`;
          let D = (t.getDate() < 10) ? `0${t.getDate()}` : `${t.getDate()}`;
          return `${Y}-${M}-${D}`;
        })(),
        way: '',
        receiver: '',
        receiveAdd: '',
        content: '',
        billFiles: [],
        receiptFiles: [],
        state: 0
      },
      showSubModal: false,
      showDelModal: false,
    };
  },
  computed: {
    contractTypeChan() {
      return (this.business.contractType.name === '') ? true : false;
    }
  },
  props: ['initBusiness', 'user'],
  mounted() {
    this.$emit('pathsChan', this.paths);

    this.bill.addUpAmount = (() => {
      var sum = 0;
      for (let i = 0; i < this.business.bills.length; i++) {
        sum += parseInt(this.business.bills[i].amount);
      }
      return sum;
    })();
  },
  methods: {
    amountCheck() {
      if (this.bill.addUpAmount + this.bill.amount > parseInt(this.business.contractAmount)) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return false;
      } else {
        return true;
      }
    },
    typeCheck() {
      if (this.bill.type === '') {
        if (this.bill.unit.address === '' || this.bill.unit.tele === '' || this.bill.unit.depositBank === '' || this.bill.unit.account === '') {
          this.$message({
            message: '',
            type: 'warning'
          });
          return false;
        } else {
          return true;
        }
      } else {
        return true;
      }
    },
    sub() {
      if (!this.amountCheck()) {
        return false;
      } else if (!this.typeCheck()) {
        return false;
      } else {
        this.showSubModal = true;
      }
    },
    submited(id) {
      this.bill.id = id;
      this.business.bills.push(this.bill);
      this.$emit('submited', this.business);
      this.showSubModal = false;
      this.$router.push({ path: 'billing-infor' });
    },
    subCanceled() {
      this.showSubModal = false;
    },
    del() {
      this.showDelModal = true;
    },
    deleted() {
      this.showDelModal = false;
      this.$router.push({ path: 'billing-infor' });
    },
    delCanceled() {
      this.showDelModal = false;
    },
  },
  components: {
    billSubModal,
    billDelModal
  }
};
</script>

<style lang="sass" scoped>
</style>



