<template>
  <form class="form-horizontal normal-wrap">
    <div class="form-group">
      <div class="col-sm-4 row">
        <label class="col-xs-3 control-label"></label>
        <div class="col-xs-9">
          <input type="text" class="form-control" placeholder="" v-model.trim="sea.requester">
        </div>
      </div>
      <div class="col-sm-4 row">
        <label class="col-xs-3 control-label"></label>
        <div class="col-xs-9">
          <input type="text" class="form-control" placeholder="" v-model.trim="sea.requesterName">
        </div>
      </div>
      <div class="col-sm-4 row">
        <label class="col-xs-3 control-label"></label>
        <div class="col-xs-9">
          <input type="text" class="form-control" placeholder="" v-model.trim="sea.applicantName">
        </div>
      </div>
    </div>
    <div class="form-group">
      <div class="col-sm-6 row">
        <label class="col-xs-2 control-label"></label>
        <div class="col-xs-10">
          <div class="input-group">
            <select class="form-control" v-model="sea.amount" @change="search()">
              <option :value="AMO" v-for="(AMO, index) in amounts" :key="index">{{AMO}}</option>
            </select>
            <div class="input-group-addon"></div>
          </div>
        </div>
      </div>
      <div class="col-sm-6 row">
        <label class="col-xs-2 control-label"></label>
        <div class="col-xs-10">
          <select class="form-control" v-model="sea.type" @change="search()">
            <option :value="TYPE" v-for="(TYPE, index) in types" :key="index">{{TYPE}}</option>
          </select>
        </div>
      </div>
    </div>
    <div class="form-group">
      <div class="col-sm-12 row">
        <label class="col-xs-1 control-label"></label>
        <div class="col-xs-5">
          <input type="date"
                 class="form-control"
                 placeholder=""
                 v-model.trim="sea.time.start"
                 @change="search()">
        </div>
        <div class="col-xs-5">
          <input type="date"
                 class="form-control"
                 placeholder=""
                 v-model.trim="sea.time.end"
                 @change="search()">
        </div>
      </div>
    </div>
    <div class="form-group">
      <div class="col-sm-12">
        <button class="btn btn-primary" type="button" @click="search">
          
        </button>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  name: 'businessCompleteSearchBar',
  data() {
    return {
      sea: {
        requester: '',
        requesterName: '',
        applicantName: '',
        time: {
          start: '',
          end: ''
        },
        amount: '',
        type: '',
      },
      amounts: [
        '',
        '0.1-2',
        '2-8',
        '8-20',
        '20'
      ],
      types: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ]
    };
  },
  methods: {
    search() {
      this.$emit('search', this.sea);
    }
  }
};
</script>

<style style="sass" scoped>
</style>



