<template>
  <div class="side-bar">
    <ul class="nav nav-pills nav-stacked">
      <router-link :to="ROU.link"
                   v-for="(ROU, index) in routes"
                   :key="index"
                   tag="li"
                   active-class="active">
        <a>{{ROU.name}}</a>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'sideBar',
  data() {
    return {};
  },
  computed: {
    routes() {
      let routes = [
        {name: '', link: '/business-complete-list'},
        {name: '', link: '/bid-info-list'},
        {name: '', link: '/rule-regulation'}
      ];
      this.$router.push(routes[0].link);
      if (this.user.department === '') {
        return routes.concat([
          {name: '', link: '/business-review-list-leader'},
          // {name: '', link: '/business-handle-list-leader'},
          {name: '', link: '/customer-infor-list'},
          {name: '', link: '/staff-management-author'},
          {name: '', link: '/staff-management-infor'},
          {name: '', link: '/business-analysis'}
        ]);
      } else if (this.user.department === '') {
        return routes.concat([
          {name: '', link: '/business-review-list-office'},
          {name: '', link: '/business-handle-list-office'}
        ]);
      } else if (this.user.department === '') {
        return routes.concat([
          {name: '', link: '/business-review-list-sales'},
          {name: '', link: '/business-handle-list-sales'},
          {name: '', link: '/customer-infor-list'}
        ]);
      } else if (this.user.department === '') {
        return routes.concat([
          {name: '', link: '/business-review-list-risk'},
          {name: '', link: '/business-handle-list-risk'}
        ]);
      } else if (this.user.department === '') {
        return routes.concat([
          {name: '', link: '/business-handle-list-archives'}
        ]);
      } else if (this.user.department === '') {
        return routes.concat([
          {name: '', link: '/business-handle-list-financial'}
        ]);
      } else if (this.user.department === '') {
        return routes.concat([
          // {name: '', link: '/bid-info-list'},
          {name: '', link: '/bid-info-draft'}
        ]);
      } else {
        return routes;
      }
    }
  },
  props: ['user'],
  methods: {}
}
</script>

<style lang="sass" scoped>
@import '../../scss/_variables.scss';

.side-bar {
  position: fixed;
  padding-top: 20px;
  .nav-pills {
    > li {
      > a {
        border-radius: 0;
      }
      &.active {
        > a {
          box-shadow: 1px 2px 4px rgba(84,173,217,1);
        }
      }
    }
  }
}
</style>



