<template>
  <div class="normal-wrap">
    <h3 class="main-title">
      
      <router-link class="btn btn-primary pull-right"
                   to="billing-infor-editor"
                   v-if="addBillShow">
        
      </router-link>
    </h3>
    <div class="com-list list-group">
      <router-link class="list-group-item"
                   :to="billRoute(BILL)"
                   v-for="(BILL, index) in business.bills"
                   :key="index">{{BILL.amount+''}}</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'billingInfor',
  data() {
    return {
      paths: [],
      business: this.initBusiness
    };
  },
  computed: {
    addBillShow() {
      return this.user.department === '' ? true : false;
    }
  },
  props: ['initBusiness', 'user'],
  mounted() {
    if (this.user.department === '') {
      this.paths.push({ name: '', url: '/business-handle-list-sales', present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-sales-${this.$route.params.id}/billing-infor`, present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-sales-${this.$route.params.id}/billing-infor`, present: true });
    } else if (this.user.department === '') {
      this.paths.push({ name: '', url: '/business-handle-list-risk', present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-risk-${this.$route.params.id}/billing-infor`, present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-risk-${this.$route.params.id}/billing-infor`, present: true });
    } else if (this.user.department === '') {
      this.paths.push({ name: '', url: '/business-handle-list-leader', present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-leader-${this.$route.params.id}/billing-infor`, present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-leader-${this.$route.params.id}/billing-infor`, present: true });
    } else if (this.user.department === '') {
      this.paths.push({ name: '', url: '/business-handle-list-office', present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-office-${this.$route.params.id}/billing-infor`, present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-office-${this.$route.params.id}/billing-infor`, present: true });
    } else if (this.user.department === '') {
      this.paths.push({ name: '', url: '/business-handle-list-financial', present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-financial-${this.$route.params.id}/billing-infor`, present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-financial-${this.$route.params.id}/billing-infor`, present: true });
    } else if (this.user.department === '') {
      this.paths.push({ name: '', url: '/business-handle-list-archives', present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-archives-${this.$route.params.id}/billing-infor`, present: false });
      this.paths.push({ name: '', url: `/business-handle-detail-archives-${this.$route.params.id}/billing-infor`, present: true });
    }
    this.$emit('pathsChan', this.paths);
  },
  methods: {
    billRoute(BILL) {
      return 'billing-infor-detail-'+BILL.id;
    }
  }
};
</script>

<style lang="sass" scoped>
</style>



